
import React from 'react';

import './css/tree.css'
import EventManager from "../../EventManager";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";



class Tree extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
            visible: false,

        }
    }

    componentDidCatch(error, errorInfo) {
        mp.trigger('client:ui:debug', 'Tree.jsx', error, errorInfo); // eslint-disable-line
    }

    componentDidMount() {
        EventManager.addHandler('Tree', value => {
            if (value.type === 'show') {
                this.setState({ show: true })
            } else if (value.type === 'hide') {
                this.setState({ show: false })
            }
        })
    }

    componentWillUnmount() {
        EventManager.removeHandler('Tree');
    }

    handleExit(status) {
        try {
            mp.trigger('client:Tree:closeTree', status); // eslint-disable-line
        }
        catch (e) {
            console.log(e);
        }
    }
    getJob() {
        try {
            mp.trigger('client:Tree:getJob'); // eslint-disable-line

        } catch (e) {
            // console.log(`Ngh vic`)
        }
    };

    render() {
        if (!this.state.show) {
            return null;
        }
        return (
            <Router>
                <div className='container-tree'>
                   
                    <div className="leftbox-tree">
                        <div className='title-job-tree'>
                                <img src={`https://thumbs.dreamstime.com/b/red-lawn-mower-cutting-grass-gardening-process-mowing-lawnmower-trimming-green-work-tool-equipment-summer-care-machine-backyard-152129336.jpg`}/>
                                <h3>Cng vic</h3>
                                <p>Ngi ct c</p>

                        </div>

                 
                    </div>
                    <div className="box-getjobtree">
                        <div className='people-tree'>
                            <img src={`https://majestic-rp.ru/img/Home/about/person.webp`}/>
                        </div>
                        <div className="description">
                            <h3>Yu cu cng vic</h3>
                            <span>Cp  1</span>
                            <span>C th xanh</span>
                        </div>
                       <div className="description-job">
                            <h3>Miu t:</h3>
                            <p> bt u cng vic bn cn phi nhn vic trc. y l cng vic tng i nh nhng dnh cho cc 
                                bn mi tham gia my ch. Cng vic ny i hi bn phi kin nhn ct tng bi c. C 3 cng c  
                                ct c, bn s ly tng cng c ng vi tng mu ca checkpoint. Lng s c chuyn v ti khon ngn hng ca bn.
                                Trn map c tng ch  bn l vic. hy chn 1 ch  bt u cng vic. Chc bn may mn
                            </p>
                        </div>
                        <div className='btn-getjob'>
                            <button onClick={() => this.getJob()}>Nhn vic</button>
                        </div>
                        <div className='btn-exit'>
                            <span onClick={(status) => this.handleExit(status)}>ng</span>
                        </div> 
                       
                    </div>
                   {/* <div className='btn-getjob'>
                            <button onClick={() => this.getJob()}>Nhn vic</button>
                        </div>
                     <div className='btn-exit'>
                        <button onClick={(status) => this.handleExit(status)}>ng</button>
                    </div> */}

                </div>

            </Router>
        )
    }
}

export default Tree;
